@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    border-radius: 3px;
    height: 40px;
    font-weight: 600;
    font-size: 15px;
    text-transform: uppercase;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-rect{
    clip-path: polygon(2% 0,100% 0,98% 100%,0 100%);
}

.m-btn-red{
    background: $red;
    color: #fff;
    &:hover{
        color: #fff;
        background: darken($red,10%);
    }
}

.m-btn-dark{
    background: #523176;
    color: #fff;
    &:hover{
        color: #fff;
        background: darken(#523176,10%);
    }
}

.m-btn-pink{
    background: #9575FF;
    color: #fff;
    &:hover{
        color: #fff;
        background: darken(#9575FF,10%);
    }
}
