.faq{
  padding: 120px 0 95px;
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    &-left{
      padding: 75px 0;
      display: flex;
      align-items: center;
    }
  }
  &__title{
    font-weight: 400;
    font-size: 61px;
    line-height: 71px;
    text-transform: uppercase;
    color: $pink;
    margin: 0 0 0 50px;
  }
  &__img{
    width: 46.59%;
    min-width: unset;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
}


@media screen and (max-width: 1020px){
  .faq{
    &__title{
      font-size: 42px;
      line-height: 120%;
      margin-left: 25px;
    }
  }
}


@media screen and (max-width: 720px){
  .faq{
    &__top{
      display: block;
      &-left{
        padding: 0 0 30px;
      }
      .wrap-img-right-trans{
        width: 50px;
        height: 50px;
      }
    }
    &__title{
      font-size: 27px;
    }
    &__img{
      width: 100%;
    }
    &__items{
      gap: 10px;
    }
  }
}
