.skin-card{
  padding: 0 18px;
  display: block;
  &__target{
    color: $pink;
    text-align: center;
    font-size: 15px;
    line-height: 25px;
  }
  &__img{
    position: relative;
    margin-top: 7px;
    &:before{
      content: '';
      position: absolute;
      width: 101.14px;
      height: 117.3px;
      background-image: url(../img/skin-bg.svg);
      background-repeat: no-repeat;
      background-size: 100% auto;
      background-position: center;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
    }
    img{
      width: 100%;
      position: relative;
      z-index: 2;
    }
  }
  &__price{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    line-height: 225%;
    color: $purple;
    margin-top: 5px;
    img{
      width: 20px;
      min-width: unset;
      margin-left: 15px;
    }
  }
  &__from{
    font-size: 15px;
    line-height: 25px;
    margin-top: 6px;
    text-align: center;
  }
  &__time{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 30px;
    margin-top: 6px;
    padding: 2px 10px;
    font-size: 13px;
    line-height: 120%;
    color: #8787AB;
    background: #313155;
    border-radius: 10px;
    img{
      margin-right: 4px;
      min-width: unset;
    }
  }
}

.catalog-card{
  padding: 10px 0 16px;
  background: linear-gradient(180deg,#492e5273 0%, #15142a 100%);
  &__hours,
  &__bought{
    font-size: 13px;
    line-height: 25px;
    text-align: center;
    color: #8787AB;
  }
  &__img{
    display: block;
    position: relative;
    width: calc(100% - 30px);
    margin: 7px auto 0;
    padding-bottom: 65%;
    &:before{
      content: '';
      position: absolute;
      width: 128.8px;
      height: 149.38px;
      background-image: url(../img/skin-bg.svg);
      background-repeat: no-repeat;
      background-size: 100% auto;
      background-position: center;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
    }
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      left: 0;
      top: 0;
      position: absolute;
      z-index: 2;
    }
  }
  &__title{
    display: block;
    margin-top: 2px;
    font-weight: 600;
    font-size: 15px;
    line-height: 25px;
    color: #fff;
    text-align: center;
    transition: all .4s;
    &:hover{
      color: $pink;
    }
  }
  &__price{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    line-height: 25px;
    color: $purple;
    margin-top: 5px;
    img{
      width: 20px;
      min-width: unset;
      margin-left: 5px;
    }
  }
  &__slots{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 5px;
    margin-top: 15px;
    padding: 0 5px;
  }
  &__slot{
    &-wrap{
      padding-bottom: 100%;
      background: #2B2A4E;
      border-radius: 5px;
      position: relative;
      img{
        position: absolute;
        max-width: 100%;
        max-height: 100%;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
      }
    }
  }
  &__btn,
  &__select{
    width: 100%;
    max-width: 138px;
    margin: 22px auto 0;
  }
  &__bought{
    margin-top: 10px;
  }
  &__select{
    position: relative;
    input{
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 2;
      width: 100%;
      height: 100%;
      cursor: pointer;
      &:hover + label{
        background: darken(#fff,10%);
      }
      &:checked+label{
        background-color: $red;
        color: #fff;
      }
      &:checked+label{
        &:before{
          content: attr(data-title2);
        }
      }
      &:checked:hover{
        background: darken($red,10%);
      }
    }
    label{
      background: #FFFFFF;
      color: $red;
      &:before{
        content: attr(data-title1);
      }
    }
  }
  &__status{
    font-size: 15px;
    line-height: 24px;
    color: #9595CB;
    text-align: center;
    &._red{
      color: $red;
    }
  }
}

.offer-card{
  display: grid;
  grid-template-columns: 175px 409px  290px 265px;
  font-weight: 600;
  font-size: 15px;
  color: #8787AB;
  padding: 20px 0;
  border-bottom: 0.5px solid #8787AB;
  &:first-child{
    padding-top: 0;
  }
  &__unblock{
    width: 110px;
    display: flex;
    align-items: center;
    padding: 0 15px;
    background: #2B2A4E;
    border-radius: 5px;
    height: 40px;
    img{
      min-width: unset;
      margin-right: 11px;
      flex-shrink: 0;
    }
  }
  &__props{
    display: flex;
    align-items: center;
    &-title{
      flex-shrink: 0;
      margin-right: 30px;
    }
  }
  .skin-props{
    flex-grow: 1;
    padding-top: 19px;
    transform: translateY(-10px);
    max-width: 270px;
    &__arrow{
      width: 16px;
    }
    &__wrap{
      height: 5px;
    }
  }
  .catalog-card__slots{
    max-width: 220px;
    margin: 0;
    padding: 0;
  }
  &__action{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .catalog-card__btn{
    margin: 0 0 0 10px;
  }
  .catalog-card__price{
    margin: 0;
  }
}

.quality-card{
  display: grid;
  grid-template-columns: calc(100% - 245px) 235px;
  align-items: center;
  gap: 15px 10px;
  padding: 10px 0;
  border-bottom: 0.5px solid #8787AB;
  font-weight: 600;
  font-size: 15px;
  color: #8787AB;
  &:first-child{
    padding-top: 0;
  }
  &__action{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .catalog-card__btn{
    margin: 0 0 0 10px;
  }
  .catalog-card__price{
    margin: 0;
  }
}

.faq-card{
  background: #232341;
  box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
  padding: 30px;
  border-radius: 10px;
  cursor: pointer;
  &._active{
    .faq-card__show{
      color: $pink;
      svg{
        transform: rotate(180deg);
      }
      path{
        stroke:$pink;
      }
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    font-size: 21px;
    line-height: 25px;
    color: #9575FF;
    transition: all .4s;
    svg{
      transition: all .4s;
      margin-left: 10px;
      flex-shrink: 0;
    }
  }
  &__hidden{
    margin-top: 20px;
    display: none;
  }
  &__text{
    font-size: 13px;
    line-height: 20px;
    max-width: 962px;
  }
}

.cart-card{
  background: #232341;
  box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
  border-radius: 10px;
  padding: 23px 90px 30px 30px;
  display: flex;
  align-items: center;
  position: relative;
  overflow: hidden;
  &__remove{
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    width: 60px;
    height: 100%;
    background: #2B2A4E;
    bottom: 0;
    right: 0;
    transition: all .3s;
    img{
      min-width: unset;
    }
    &:hover{
      background: darken(#2B2A4E,10%);
    }
  }
  &__img{
    width: 150px;
    flex-shrink: 0;
    margin-right: 30px;
  }
  .catalog-card__img{
    &:before{
      width: 85.71px;
      height: 99.41px;
    }
  }
  &__text{
    font-size: 17px;
    line-height: 25px;
    color: #8787AB;
    margin: 0;
  }
  &__title{
    font-weight: 600;
    font-size: 31px;
    line-height: 120%;
    margin: 3px 0 0;
  }
  .offer-card__unblock{
    display: inline-flex;
    width: unset;
    padding: 0 11px;
    margin: 10px 0 0;
    font-size: 15px;
    color: #8787AB;
  }
  .skin-card__price{
    margin: 30px 0 0;
    justify-content: unset;
    font-size: 31px;
    line-height: unset;
    img{
      width: 30px;
    }
  }
}

.catalog-card2{
  .catalog-card__img{
    margin-top: 0;
  }
}


@media screen and (max-width: 1250px){
  .offer-card{
    grid-template-columns: 120px calc(100% - 555px) 180px 225px;
    gap: 20px 10px;
    &__props{
      &-title{
        margin-right: 10px;
      }
    }
  }
}


@media screen and (max-width: 1150px){
  .cart-card{
    padding-right: 70px;
    &__img{
      margin-right: 15px;
    }
  }
}


@media screen and (max-width: 820px){
  .offer-card{
    grid-template-columns: auto 180px;
    .offer-card__props,
    &__action{
      order: 2;
      grid-column: 1/-1;
    }
    [data-title]{
      position: relative;
      margin-top: 15px;
      &:before{
        content: attr(data-title);
        position: absolute;
        bottom: 100%;
        left: 0;
        font-size: 12px;
        margin-bottom: 5px;
      }
    }
    .skin-props{
      max-width: 100%;
    }
  }

  .quality-card{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 720px){
  .faq-card{
    padding: 15px;
    &__show{
      font-size: 18px;
    }
  }

  .cart-card{
    padding: 15px 60px 15px 15px;
    &__remove{
      width: 50px;
    }
    &__title{
      font-size: 24px;
    }
    .skin-card__price{
      font-size: 24px;
      img{
        width: 24px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .offer-card{
    grid-template-columns: 100%;
    .catalog-card__slots{
      max-width: 270px;
    }
  }

  .cart-card{
    padding: 15px 15px 65px;
    display: block;
    text-align: center;
    &__img{
      margin: 0 auto 20px;
    }
    .skin-card__price{
      justify-content: center;
    }
    &__remove{
      height: 50px;
      width: 100%;
    }
  }
}
