.skin{
  overflow: hidden;
  padding: 160px 0 25px;
  &__top{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    text-align: right;
    &-img{
      margin-left: 50px;
      flex-shrink: 0;
    }
  }
  &__title{
    font-weight: 400;
    font-size: 61px;
    line-height: 71px;
    text-transform: uppercase;
    color: $pink;
    margin: 0;
  }
  &__desc{
    margin: 0;
    font-size: 41px;
    line-height: 48px;
    color: #9575FF;
    text-transform: uppercase;
  }
}

.skin-info{
  margin-top: 41px;
  background: #232341;
  box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
  padding: 30px;
  border-radius: 10px;
  display: grid;
  grid-template-columns: 350px calc(100% - 410px);
  gap: 60px;
  .catalog-card__img{
    padding: 0;
    img{
      position: static;
      height: auto;
    }
    &:before{
      width: 200px;
      height: 231.96px;
    }
  }
  &__links{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 36.5px;
  }
  &__link{
    display: flex;
    align-items: center;
    font-size: 17px;
    line-height: 25px;
    color: #8787AB;
    transition: all .4s;
    svg{
      margin: 10px;
    }
    path{
      transition: all .4s;
    }
    &:hover{
      color: #fff;
      path{
        stroke: #fff;
      }
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 37px;
  }
  &__price{
    padding: 0 13px 0 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 170px;
    height: 50px;
    background: #313155;
    border-radius: 10px;
    font-weight: 600;
    font-size: 17px;
    line-height: 25px;
    color: $purple;
    img{
      min-width: unset;
    }
  }
  &__btn{
    width: 138px;
    height: 50px;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__title,
  &__title3{
    font-size: 17px;
    line-height: 25px;
    color: #8787AB;
    margin: 0;
  }
  &__title2{
    margin: 5px 0 0;
    font-weight: 600;
    font-size: 31px;
    line-height: 25px;
    color: #FFFFFF;
    text-transform: unset;
  }
  &__title3{
    margin: 5px 0 0;
  }
  .catalog__price{
    flex-shrink: 0;
    &-num{
      margin-right: 10px;
      img{
        margin-left: 5px;
      }
    }
  }
  &__props{
    margin-top: 23px;
    &-title{
      font-weight: 600;
      font-size: 24px;
      line-height: 25px;
      color: #9575FF;
      margin: 0 0 10px;
    }
    &-items{
      margin-top: 27px;
    }
    &-item{
      min-height: 40px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 5px 0;
      font-weight: 600;
      font-size: 15px;
      line-height: 120%;
      &:not(:last-child){
        border-bottom: 0.5px solid #8787AB;
      }
    }
    &-desc{
      color: #8787AB;
    }
  }
  &__slots{
    margin-top: 28px;
    display: flex;
    flex-wrap: wrap;
    .catalog-card__slot{
      margin: 5px 10px 0 0;
      width: 50px;
    }
  }
}

.skin-props{
  padding-top: 26px;
  position: relative;
  &__wrap{
    display: flex;
    height: 10px;
    border-radius: 40px;
    overflow: hidden;
  }
  &__arrow{
    transform: translateX(-50%);
    position: absolute;
    top: 0;
    min-width: unset;
    width: 24px;
  }
  &__item{
    height: 100%;
    &--purple{
      background: #9575FF;
    }
    &--green{
      background: #8CBA52;
    }
    &--orange{
      background: #F89941;
    }
    &--pink{
      background: $pink;
    }
    &--red {
      background: $red;
    }
  }
}

.offer{
  padding: 25px 0 95px;
  &__title{
    font-weight: 600;
    font-size: 31px;
    line-height: 25px;
    color: #9575FF;
    text-transform: unset;
  }
  &__head{
    display: grid;
    grid-template-columns: 204px 409px 290px 295px;
    justify-content: space-between;
    font-size: 17px;
    line-height: 25px;
    color: #8787AB;
  }
  &__wrap{
    background: #232341;
    box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
    padding: 30px;
    border-radius: 10px;
    margin-top: 10px;
  }
}

.qualities{
  margin-top: 40px;
  &__title{
    font-weight: 600;
    font-size: 21px;
    line-height: 25px;
    margin: 0;
  }
  &__head{
    display: grid;
    grid-template-columns: calc(100% - 245px) 235px;
    gap: 10px;
    font-size: 17px;
    line-height: 25px;
    color: #8787AB;
    margin-top: 10px;
  }
  &__items{
    margin-top: 12px;
  }
}

.offer-desc{
  margin-top: 40px;
  &__title{
    font-weight: 600;
    font-size: 21px;
    line-height: 25px;
    margin: 0;
  }
  &__text{
    margin-top: 10px;
    font-size: 17px;
    line-height: 25px;
    color: #8787AB;
    p{
      margin: 0;
      font-size: inherit;
      line-height: inherit;
    }
  }
}


@media screen and (max-width: 1250px){
  .offer{
    &__head{
      grid-template-columns: 150px calc(100% - 615px) 180px 255px;
      gap: 10px;
    }
  }
}


@media screen and (max-width: 1020px){
  .skin-info{
    grid-template-columns: 100%;
    .catalog-card__img{
      max-width: 400px;
    }
  }
}



@media screen and (max-width: 820px){
  .offer{
    &__head{
      display: none;
    }
  }

  .qualities{
    &__head{
      display: none;
    }
    &__items{
      margin-top: 30px;
    }
  }
}


@media screen and (max-width: 720px){
  .skin{
    padding: 120px 0 25px;
    &__top{
      &-img{
        margin-left: 10px;
      }
      .wrap-img-right-trans{
        width: 50px;
        height: 50px;
      }
    }
    &__title{
      font-size: 21px;
      line-height: 27px;
    }
    &__desc{
      font-size: 17px;
      line-height: 19px;
    }
  }

  .skin-info{
    padding: 15px;
    &__price{
      width: 150px;
      padding-left: 13px;
    }
    &__top{
      display: block;
    }
    &__title2{
      line-height: 110%;
      font-size: 24px;
    }
    .catalog__price{
      justify-content: unset;
    }
    &__props{
      &-title{
        font-size: 20px;
      }
    }
  }

  .skin-props{
    &__arrow{
      width: 18px;
    }
  }

  .offer{
    padding-bottom: 50px;
    &__title{
      font-size: 24px;
    }
    &__wrap{
      padding: 15px;
    }
  }
}
