.lk-top{
  padding: 165px 0 30px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  text-align: right;
  &__title{
    font-weight: 400;
    font-size: 61px;
    line-height: 71px;
    text-transform: uppercase;
    color: $pink;
    margin: 0 50px 0 0;
  }
  &__subtitle{
    margin: 0;
    font-size: 41px;
    line-height: 48px;
    color: #9575FF;
    text-transform: uppercase;
  }
  &__left{
    margin-right: 50px;
    .lk-top__title{
      margin: 0;
    }
  }
}

.lk{
  overflow: hidden;
  padding-bottom: 70px;
  &__wrap{
    background: #232341;
    box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
    padding: 30px;
    border-radius: 10px;
  }
  &__top{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
  }
  &__action{
    margin-top: 25px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__btn{
    font-size: 18px;
    line-height: 24px;
    text-decoration: underline;
    background-color: transparent;
    color: #F851D7;
    margin-left: 20px;
    &:hover{
      text-decoration: none;
    }
  }
}

.user-info{
  display: flex;
  align-items: center;
  &__img{
    width: 200px;
    height: 232px;
    flex-shrink: 0;
    margin-right: 45px;
    padding: 41px 25px;
    background-image: url(../img/skin-bg.svg);
    background-repeat: no-repeat;
    background-size: 100% auto;
    background-position: center;
    img{
      display: block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }
  }
  &__content{
    flex-grow: 1;
  }
  &__title{
    font-weight: 600;
    font-size: 31px;
    line-height: 25px;
    color: #FFFFFF;
    margin: 0;
  }
  &__desc{
    margin: 5px 0 0;
    font-weight: 400;
    font-size: 17px;
    line-height: 25px;
    color: #8787AB;
  }
  &__items{
    margin-top: 20px;
    max-width: 307px;
    width: 100%;
  }
  &__item{
    color: #8787AB;
    font-size: 17px;
    line-height: 35px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-title,
    &-desc{
      line-height: inherit;
      font-size: inherit;
    }
    &-desc{
      display: flex;
      align-items: center;
      justify-content: flex-end;
    }
  }
  &__status{
    font-weight: 700;
    color: #9575FF;
  }
  &__percent{
    font-weight: 700;
    color: $pink;
  }
  &__detail{
    margin-left: 15px;
    color: inherit;
    text-decoration: underline;
    &:hover{
      text-decoration: none;
    }
  }
}

.user-settings{
  &__fields{
    margin-top: 36px;
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
  }
  &__field{
    display: grid;
    grid-template-columns: 1fr 1.4fr 76px;
    gap: 20px;
    align-items: center;
    input{
      height: 40px;
    }
  }
  &__title{
    text-align: right;
  }
  &__btn{
    padding: 0;
    background-color: transparent;
    font-weight: 600;
    font-size: 15px;
    line-height: 25px;
    color: $pink;
  }
}

.user-finance{
  display: grid;
  grid-template-columns: 100%;
  gap: 10px;
  max-width: 354px;
  margin-left: auto;
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 17px;
    line-height: 25px;
    color: #8787AB;
  }
  &__desc{
    display: flex;
    align-items: center;
    font-size: 20px;
    line-height: 25px;
    color: $purple;
    margin-left: 10px;
    img{
      width: 20px;
      min-width: unset;
      margin-left: 5px;
    }
  }
}

.order-table{
  margin-top: 47px;
  overflow-x: auto;
  padding-bottom: 15px;
  table{
    width: 600px;
    min-width: 100%;
  }
  th,
  td{
    color: #8787AB;
    line-height: 120%;
    text-align: left;
    &:last-child{
      text-align: right;
    }
  }
  th{
    height: 40px;
    font-size: 17px;
    text-transform: uppercase;
  }
  td{
    height: 50px;
    font-size: 15px;
    border-top: 0.5px solid #8787AB;
  }
  .catalog-card__price{
    justify-content: unset;
    font-weight: 600;
  }
}


@media screen and (max-width: 1100px){
  .user-info{
    &__img{
      margin-right: 20px;
    }
  }
}


@media screen and (max-width: 1020px){
  .lk-top{
    &__title{
      font-size: 42px;
      line-height: 120%;
      margin-left: 25px;
    }
    &__subtitle{
      font-size: 35px;
      line-height: 120%;
    }
  }

  .lk{
    &__top{
      grid-template-columns: 100%;
    }

  }

  .user-finance{
    margin-left: 0;
  }

  .user-settings{
    &__field{
      grid-template-columns: 120px 1.4fr 76px;
    }
  }
}


@media screen and (max-width: 720px){
  .lk-top{
    padding-top: 100px;
    &__title{
      font-size: 27px;
      margin-right: 30px;
    }
    &__subtitle{
      font-size: 22px;
    }
    &__left{
      margin-right: 30px;
    }
    .wrap-img-right-trans{
      width: 50px;
      height: 50px;
    }
  }

  .lk{
    padding-bottom: 40px;
    &__wrap{
      padding: 15px;
    }
  }

  .user-info{
    display: block;
    &__img{
      margin: 0 auto 20px;
    }
    &__items{
      max-width: 100%;
    }
    &__title{
      font-size: 24px;
      line-height: 120%;
    }
  }

  .user-finance{
    max-width: 100%;
  }

  .user-settings{
    &__field{
      grid-template-columns: calc(100% - 96px) 76px;
    }
    &__title{
      grid-column: 1/-1;
      text-align: left;
    }
  }

  .order-table{
    padding: 0 15px 15px;
    width: calc(100% + 30px);
    margin-left: -15px;
  }
}
