.custom-select{
  .nice-select{
    background: transparent !important;
    border: none;
    color: #fff;
    width: auto;
    padding: 0;
    .current{
      display: flex;
      align-items: center;
      width: 100%;
      height: 100%;
      padding-right: 25px;
      background-image: url(../img/select-arrow.svg);
      background-repeat: no-repeat;
      background-position: right center;
    }
    .list{
      z-index: 10;
      background: #232341;
      box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
      border: none;
      width: unset;
      min-width: 100%;
    }
    .option{
      border: none !important;
      &.selected,
      &:hover{
        background-color: transparent !important;
        font-weight: 400;
        &:hover{
          color: $pink;
        }
      }
    }
  }
}

.fg {
  position: relative;
  margin: 0;
  padding: 0;
  border: none;
  .nice-select{
    width: 100%;
    padding: 0;
    height: 50px;
  }
  .nice-select .current,
  input {
    outline: none !important;
    border: none;
    background-color: #313155;
    border-radius: 10px;
    padding: 0 20px;
    height: 50px;
    width: 100%;
    font-size: 15px;
    color: #fff;
    transition: all .3s;
    &::placeholder {
      color: #8787AB;
    }
    &:focus{
      box-shadow: 0 0 2px 2px rgba($pink,.7);
    }
  }
  .nice-select .current{
    background-position: right 18px center;
    padding-right: 45px;
  }
  .option{
    font-size: 15px;
    padding: 8px 20px;
  }
  label{
    display: block;
    margin-bottom: 5px;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    color: $pink;
  }
}

.m-check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        background-image: url(../img/check.svg);
        background-repeat: no-repeat;
        background-position: center;
      }
    }
  }
  label {
    min-height: 24px;
    font-size: 15px;
    line-height: 120%;
    cursor: pointer;
    display: flex;
    align-items: center;
    color: #FFFFFF;
    &:before {
      content: '';
      z-index: 1;
      background: #313155;
      border-radius: 5px;
      width: 24px;
      height: 24px;
      flex-shrink: 0;
      margin-right: 16px;
    }
  }
}
