.sail{
  &__wrap{
    background: #232341;
    box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
    border-radius: 10px;
    padding: 30px;
  }
  &__top{
    padding-top: 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__url{
    width: 550px;
    label{
      position: absolute;
      bottom: 100%;
      left: 0;
    }
  }
  &__price{
    display: flex;
    align-items: center;
    &-title{
      font-size: 17px;
      line-height: 25px;
      margin-right: 20px;
      color: #8787AB;
    }
    .skin-card__price{
      margin: 0;
      font-size: 20px;
      line-height: 25px;
      img{
        margin-left: 10px;
      }
    }
  }
  &__nav{
    margin-top: 20px;
    display: flex;
    align-items: center;
    .catalog__search{
      margin: 0 40px 0 0;
      order: unset;
      input{
        height: 50px;
      }
    }
    .catalog__select{
      margin-right: 40px;
      order: unset;
      @media screen and (min-width: 720px){
        .nice-select{
          font-size: 17px;
        }
      }
    }
  }
  &__all{
    margin-left: auto;
    position: relative;
    input{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 2;
      cursor: pointer;
      opacity: 0;
      &:checked+label{
        &:before{
          content: attr(data-title2);
        }
      }
      &:hover+label{
        text-decoration: none;
      }
    }
    label{
      font-size: 17px;
      line-height: 25px;
      text-decoration-line: underline;
      color: $pink;
      font-weight: 400;
      &:before{
        content: attr(data-title1);
      }
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 10px;
    margin-top: 30px;
  }
}

.sail-info{
  margin-top: 40px;
  &__title,
  &__desc{
    margin: 0;
    font-size: 21px;
    line-height: 24px;
  }
  &__desc{
    color: #9595CB;
  }
  &__btn{
    width: 169.81px;
    margin-top: 35px;
  }
}

.cart-sail{
  padding-bottom: 60px;
  &__title{
    font-size: 15px;
    line-height: 24px;
    color: #9595CB;
  }
}


@media screen and (max-width: 1100px){
  .sail{
    &__nav{
      .catalog__search,
      .catalog__select{
        margin-right: 20px;
      }
    }
    &__grid{
      grid-template-columns: repeat(4,1fr);
    }
  }
}


@media screen and (max-width: 1020px){
  .sail{
    &__top{
      display: grid;
      grid-template-columns: 100%;
      gap: 20px;
      padding: 0;
    }
    &__url{
      width: 100%;
      label{
        position: static;
      }
    }
    &__nav{
      display: grid;
      grid-template-columns: 40% auto;
      gap: 20px;
      .catalog__search,
      .catalog__select{
        margin: 0;
      }
    }
    &__all{
      grid-column: 1/-1;
      margin-right: auto;
    }
    &__grid{
      grid-template-columns: repeat(3,1fr);
    }
  }
}

@media screen and (max-width: 820px){
  .sail{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 5px));
    }
  }
}

@media screen and (max-width: 720px){
  .sail{
    &__wrap{
      padding: 15px;
    }
    &__price{
      display: block;
      &-title{
        margin: 0 0 5px;
      }
      .skin-card__price{
        justify-content: unset;
      }
    }
  }
}


@media screen and (max-width: 450px){
  .sail{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
