.withdraw{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  margin-top: 50px;
  &__slots{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 10px;
  }
  &__slot{
    border: 1px solid #313155;
    border-radius: 10px;
    height: 70px;
  }
  &__fields{
    margin-top: 26px;
    display: grid;
    grid-template-columns: 100%;
    gap: 20px;
  }
  &__sum{
    display: grid;
    grid-template-columns: 100%;
    gap: 7px;
    margin-top: 40px;
    &-item{
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 19px;
      line-height: 120%;
      color: #9595CB;
    }
    .skin-card__price{
      font-size: 20px;
      line-height: 25px;
      img{
        width: 20px;
        margin-left: 10px;
      }
    }
  }
  &__btn{
    margin-top: 40px;
    width: 210px;
  }
  &__subtitle{
    margin: 0;
    font-weight: 600;
    font-size: 21px;
    line-height: 25px;
    color: #9575FF;
  }
  &__history{
    margin-top: 15px;
    table{
      width: 100%;
    }
    th,
    td{
      color: #8787AB;
      text-align: left;
      &:last-child{
        text-align: right;
      }
    }
    th{
      padding: 5px 0;
      font-weight: 400;
      font-size: 17px;
      line-height: 25px;
    }
    td{
      height: 50px;
      padding: 10px 0;
      font-weight: 600;
      font-size: 15px;
      border-top: 0.5px solid #8787AB;
    }
    .skin-card__price{
      font-size: 17px;
      line-height: 25px;
      justify-content: flex-end;
      img{
        margin-left: 10px;
      }
    }
  }
  &__currency{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 0 40px;
    label{
      grid-column: 1/-1 ;
    }
    input{
      padding-right: 45px;
    }
    &-item{
      position: relative;
    }
    &-icon{
      font-size: 17px;
      line-height: 25px;
      height: 50px;
      position: absolute;
      top: 50%;
      right: 12px;
      transform: translateY(-50%);
      color: $purple;
      z-index: 2;
      display: flex;
      align-items: center;
    }
  }
}


@media screen and (max-width: 1020px){
  .withdraw{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 1020px){
  .withdraw{
    &__currency{
      gap: 0 20px;
    }
  }
}
