@media screen and (max-width: 768px){
  .container-header{
    width: 100%;
  }
  .head-modal-menu{

  }
}

*,
*:before,
*:after{
  box-sizing: border-box;
}
.bg-sell-right,
.bg-sell-left{
  pointer-events: none;
  z-index: -1;
}

.header__lk{
  position: absolute;
  right: 43px;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  .catalog__price{
    margin-left: 0;
    &-num{
      margin-right: 15px;
    }
  }
}

.header__user{
  margin-left: 35px;
  display: block;
}



@media screen and (max-width: 1350px){
  @media screen and (min-width: 768px){
    .header__menu{
      padding: 0 40px;
    }
    .header__lk{
      width: 100%;
      right: 0;
      .catalog__price{
        position: absolute;
        left: 20px;
        top: 50%;
        transform: translateY(-50%);
      }
    }
    .header__user{
      margin-left: 0;
      position: absolute;
      right: 20px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}


@media screen and (max-width: 1070px){
  .header__lk{
    @media screen and (min-width: 768px){
      .catalog__price{
        display: flex;
        justify-content: unset;
        margin-top: 0;
        &-num{
          display: none;
        }
      }
    }
  }
}

@media screen and (max-width: 768px){
  .header__lk{
    transition: all .0s;
    right: unset;
    left: 20px;
    .catalog__price{
      margin: 0;
      order: unset;
    }
  }
  .header__user{
    margin-left: 20px;
  }
  .head-modal-menu[style="display: block;"]{
    &~.header__lk{
      opacity: 0;
      display: none;
    }
  }
}

.page{
  background-image: url(../img/bg-sell-right.png),url(../img/bg-sell-left.png);
  background-repeat: no-repeat;
  &--skins{
    background-position: right top 367px, left top 1389px;
  }
  &--faq{
    background-position: right top 367px, left top 889px;
  }
  &--lk{
    background-position: right top, left top 213px;
  }
}

.wrapper{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  .footer{
    margin-top: auto;
  }
}
