.transaction--skins{
  overflow: hidden;
  padding: 96px 0 15px;
  margin: 0;
  @media screen and (min-width: 1011px){
    .left-trans{
      img{
        max-width: unset;
        width: 115.6%;
      }
    }
  }
  .right-trans{
    h2{
      white-space: nowrap;
      font-weight: 400;
      line-height: 115%;
    }
  }
}

.skins{
  padding: 20px 26px 24px;
  background: #232341;
  border-radius: 10px;
  width: 100%;
  position: relative;
  box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
  .swiper{
    &-pagination{
      display: none;
    }
  }
}

.catalog{
  padding: 15px 0 95px;
  &__nav{
    background: #232341;
    box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
    padding: 15px 25px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
    z-index: 2;
  }
  &__btn{
    flex-shrink: 0;
    width: 184.81px;
    margin-right: 20px;
  }
  &__search{
    flex-grow: 1;
    margin: 0 20px;
    max-width: 435px;
    &-btn{
      background-color: transparent;
      padding: 0;
      position: absolute;
      right: 13px;
      top: 50%;
      transform: translateY(-50%);
    }
    input{
      padding-right: 40px;
    }
  }
  &__price{
    display: flex;
    align-items: center;
    color: $purple;
    margin-left: 20px;
    font-size: 20px;
    line-height: 125%;
    img{
      min-width: unset;
      flex-shrink: 0;
    }
    &-num{
      display: flex;
      align-items: center;
      margin-right: 20px;
      img{
        margin-left: 10px;
      }
    }
  }
  &__wrap{
    margin-top: 12px;
    display: grid;
    grid-template-columns: 240px calc(100% - 250px);
    gap: 10px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 7.5px));
    gap: 10px;
  }
  &__filter-toggle{
    display: none;
  }
}

.filter{
  padding: 20px;
  background: #232341;
  box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
  display: grid;
  grid-template-columns: 100%;
  gap: 30px;
  &__close{
    display: none;
  }
  &__group{
    &._active{
      .filter__show{
        img{
          transform: rotate(180deg);
        }
      }
    }
  }
  &__show{
    cursor: pointer;
    font-weight: 600;
    font-size: 15px;
    line-height: 200%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    img{
      min-width: unset;
      flex-shrink: 0;
      margin-left: 5px;
      transition: all .4s;
    }
  }
  &__hidden{
    margin-top: 10px;
    display: none;
  }
  &__prices{
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
  }
  &__price{
    input{
      height: 40px;
      background-image: url(../img/rouble2.svg);
      background-repeat: no-repeat;
      background-position: right 20px center;
      padding-right: 50px;
    }
  }
  &__checks{
    display: grid;
    grid-template-columns: 100%;
    gap: 14px;
  }
  &__btn{
    width: calc(100% - 24px);
    margin: 20px auto 0;
    font-size: 13px;
  }
}

.pagination{
  margin: 40px 0 0 -250px;
  width: calc(100% + 250px);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  line-height: 21px;
  letter-spacing: -0.02em;
  &__title{
    margin-right: 24px;
  }
  &__item{
    width: 36px;
    height: 36px;
    background: #313155;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #8787AB;
    margin: 0 5px;
    transition: all .4s;
    &._active{
      color: #fff;
      background-color: $red;
      pointer-events: none;
    }
    &:hover{
      color: #fff;
      background-color: $red;
    }
  }
}

.categories{
  margin-top: 34px;
  font-size: 13px;
  line-height: 24px;
  color: #8787AB;
  &__title,
  &__links{
    font-size: inherit;
    line-height: inherit;
  }
  &__title{
    margin: 0;
  }
  &__links{
    a{
      transition: all .4s;
      color: inherit;
      text-decoration: underline;
      &:hover{
        color: $pink;
      }
    }
  }
}

.desc{
  margin-top: 41px;
  display: grid;
  grid-template-columns: 100%;
  gap: 30px;
  &__title{
    font-size: 15px;
    line-height: 24px;
    color: $red;
    font-weight: 400;
    margin: 0;
  }
  &__text{
    font-size: 15px;
    line-height: 24px;
    color: #9595CB;
    p{
      line-height: inherit;
      font-size: inherit;
      margin: 0;
    }
  }
}


@media screen and (max-width: 1020px){
  .catalog{
    &__nav{
      padding: 15px;
      display: grid;
      grid-template-columns: 40% auto;
    }
    &__search{
      grid-column: 1/-1;
      order: 1;
      max-width: 100%;
      width: 100%;
      margin: 0 0 20px;
    }
    &__select{
      order: 2;
      width: 45%;
    }
    &__price{
      order: 3;
      width: 100%;
      margin: 20px 0 0;
      justify-content: center;
      grid-column: 1/-1;
    }
    &__btn{
      grid-column: 1/-1;
      order: 4;
      margin: 20px auto 0;
    }
    &__grid{
      grid-template-columns: repeat(3,calc(33.33% - (20px / 3)));
    }
  }
}


@media screen and (max-width: 820px){
  .catalog{
    padding-bottom: 45px;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 5px));
    }
  }
}


@media screen and (max-width: 720px){
  h2{
    line-height: 32px;
  }

  .catalog{
    &__wrap{
      grid-template-columns: 100%;
    }
    &__sidebar{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100vh;
      z-index: 10000;
      padding-top: 40px;
      background: #232341;
      transition: all .4s;
      animation: unset;
      opacity: 1 !important;
      transform: translateX(-100%) !important;
      &._active{
        transform: translateX(0) !important;
      }
    }
    &__filter-toggle{
      display: flex;
      width: 100%;
      margin-top: 20px;
    }
  }

  .filter{
    box-shadow: none;
    height: 100%;
    width: 100%;
    overflow-y: auto;
    &__close{
      display: block;
      position: absolute;
      top: 20px;
      right: 20px;
      background-color: transparent;
    }
  }

  .pagination{
    width: 100%;
    margin: 40px 0 0;
    &__title{
      margin-right: 10px;
    }
  }
}

@media screen and (max-width: 620px){
  .skins{
    grid-template-columns: repeat(2,calc(100% / 2));
    .swiper{
      &-pagination{
        display: flex;
        justify-content: center;
        position: static;
        margin-top: 30px;
      }
    }
  }
}


@media screen and (max-width: 450px){
  .catalog{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}
