.cart{
  &__desc{
    font-size: 15px;
    line-height: 24px;
    color: #9595CB;
    margin: 0 0 30px;
  }
  &__wrap{
    display: grid;
    grid-template-columns: calc(100% - 525px) 500px;
    gap: 25px;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 10px;
  }
}

.cart-info{
  position: sticky;
  top: 110px;
  background: #232341;
  box-shadow: 0 0 9px 4px rgba(2,2,2,.2);
  border-radius: 10px;
  padding: 30px;
  &__count,
  &__price{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 600;
    font-size: 31px;
  }
  &__price{
    color: #9575FF;
    margin-top: 25px;
  }
  .skin-card__price{
    margin: 0 0 0 30px;
    justify-content: unset;
    font-size: 31px;
    line-height: unset;
    img{
      width: 30px;
    }
  }
  &__nav{
    padding: 40px 0 0;
    margin-top: 25px;
    border-top: 0.5px solid #8787AB;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__clear{
    font-size: 18px;
    line-height: 24px;
    text-decoration-line: underline;
    color: #F851D7;
    background-color: transparent;
    padding: 0;
    &:hover{
      text-decoration: none;
    }
  }
  &__buy{
    width: 138px;
  }
  &__conf{
    margin-top: 40px;
    font-size: 15px;
    line-height: 24px;
    color: #9595CB;
    a{
      color: inherit;
      text-decoration: underline;
      &:hover{
        text-decoration: none;
      }
    }
  }
}




@media screen and (max-width: 1150px){
  .cart{
    &__wrap{
      grid-template-columns: calc(100% - 450px) 425px;
    }
  }
}


@media screen and (max-width: 1020px){
  .cart{
    &__wrap{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 720px){
  .cart-info{
    padding: 15px;
    &__count,
    &__price{
      font-size: 24px;
    }
    .skin-card__price{
      font-size: 24px;
      img{
        width: 24px;
      }
    }
    &__nav{
      flex-direction: column;
      padding-top: 25px;
    }
    &__buy{
      margin-top: 15px;
    }
    &__conf{
      font-size: 14px;
    }
  }
}
